.wrapper-link {
    text-decoration: none;
    width: 100%;
}

.card {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    border-radius: 12px;
    padding: 30px;
    margin: 25px;
    min-height: 300px;
    border-bottom: 5px solid yellowgreen;
}

.card:hover {
    box-shadow: 0px 0px 10px rgb(0, 0, 0, 0.3);
}

@media screen and (min-width: 768px) {
    .wrapper-link {
        max-width: 380px;
    }
}

.name {
    font-family: "Roboto";
    font-size: 28px;
    margin-bottom: 15px;
    color: rgb(41, 32, 32);
}

.card:hover > .name {
    color: teal;
}

.description {
    font-family: "Roboto";
    word-break: break-word;
    color: gray;
    flex-grow: 1;
}

.author {
    font-family: "Roboto";
    font-size: 18px;
    color: dimgray;
}

.profile-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.profile-icon {
    width: 60px;
    height: 60px;
    border-radius: 30px;
}
.language {
    font-weight: 100;
    font-family: "Roboto";
    color: black;
}
.tag-container {
    display: flex;
}

.tag {
    display: flex;
    align-items: center;
    margin-right: 30px;
}

.tag-icon {
    margin-right: 10px;
    font-size: 22px;
}

.tag-name {
    font-family: "Roboto";
    color: black;
}

.star-icon {
    color: rgb(145, 142, 0);
}

.fork-icon {
    color: rgb(101, 151, 0);
}