.filter-container {
    display: flex;
    flex-direction: column;
}

.filter-name {
    font-size: 12px;
    font-family: "Roboto";
    text-transform: uppercase;
    font-weight: bold;
}

.item-selector {
    padding: 10px;
    border-radius: 5px;
    background-color: #fff;
    border: 2px solid gray;
    cursor: pointer;
}

.item {
    padding: 10px;
    cursor: pointer;
}