.app-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
}

.header-container {
    margin-bottom: 30px;
}

@media screen and (min-width: 768px) {
    .header-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 80%;
    }
}

.logo-container {
    display: flex;
    flex-direction: column;
}

.main-logo {
    width: 120px;
    height: 40px;
}

.main-heading {
    font-family: "Roboto";
}

.nothing-to-show {
    font-family: "Roboto";
    color: gray;
}